import { mapValuesToThis } from '@jscrpt/common';
/**
 * Rich text block model for properties editor
 */

export class RichTextBlockModel {
  //######################### constructor #########################
  constructor(value) {
    //######################### public properties #########################

    /**
     * @inheritdoc
     */
    this.content = '';
    mapValuesToThis.bind(this)(value);
  }

}